<template lang="html">
  <div @mousemove="mousePosChanged">
    <router-view v-if="!loading"></router-view>
    <div class="loader" :class="{'loader--visible': loading}">
      <div class="loader__text">
        <span>L</span>
        <span>O</span>
        <span>A</span>
        <span>D</span>
        <span>I</span>
        <span>N</span>
        <span>G</span>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex';
  import { TimelineMax } from 'gsap';

  const assetsLoader = require('assets-loader');

  export default {
    name: 'App',
    data() {
      return {
        assetsList: [],
        loading: true,
      };
    },
    computed: {
      ...mapGetters([
        'mousePosition',
        'projects',
        'links',
      ]),
    },
    watch: {
      $route() {
        this.checkExperimentLoading();
      },
    },
    methods: {
      checkExperimentLoading() {
        const path = this.$route.path;
        if (path === '/project/experiments') {
          this.loading = true;
          let expProject = this.projects.filter(project => project.slug === 'experiments');
          expProject = expProject[0];
          const expVideoList = [];
          for (let j = 0; j < expProject.blocks.length; j++) {
            const currentBlock = expProject.blocks[j];
            for (let k = 0; k < currentBlock.videos.length; k++) {
              const video = currentBlock.videos[k];
              expVideoList.push({ url: video.src, blob: true });
            }
          }

          assetsLoader({ assets: expVideoList })
          .on('complete', () => {
            this.loading = false;
          })
          .start();
        }
      },
      addElementToAssetList(element) {
        if (element.type === 'video') {
          this.assetsList.push({ url: element.src, blob: true });
        } else if (element.type === 'image') {
          this.assetsList.push(element.src);
        } else if (element.type === 'double-images') {
          const images = element.images;
          for (let i = 0; i < images.length; i++) {
            this.assetsList.push(images[i].src);
          }
        }
      },
      loadAssets() {
        for (let i = 0; i < this.projects.length; i++) {
          for (let j = 0; j < this.projects[i].blocks.length; j++) {
            const currentBlock = this.projects[i].blocks[j];
            this.addElementToAssetList(currentBlock);
          }
        }

        assetsLoader({
          assets: this.assetsList,
        })
        .on('complete', () => {
          this.loading = false;
        })
        .start();
      },
      mousePosChanged(e) {
        this.$store.dispatch('setAnimatedMouseX', e.clientX);
        this.$store.dispatch('setAnimatedMouseY', e.clientY);
      },
      leaveAnimations(callback) {
        const child = this.$children[0];
        const page = child.$refs.page;
        page.className += ' page--animated';
        setTimeout(() => {
          callback();
          page.classList.remove('page--animated');
        }, 1500);
      },
      launchLoadingAnimations() {
        const finishAnimProps = {
          filter: 'blur(0px)',
          autoAlpha: 1,
          repeat: 1,
          yoyo: true,
        };

        const tl = new TimelineMax({
          onComplete: () => {
            tl.restart();
          },
        });
        tl.staggerTo(
          '.loader .loader__text span', 0.8,
          finishAnimProps,
          0.07,
        );
      },
    },
    beforeMount() {
      const body = document.body;
      body.addEventListener('click', (e) => {
        this.$store.dispatch('moveMouse', { x: e.clientX, y: e.clientY });
      });
      body.click();

      const newWidth = window.innerWidth ||
        document.documentElement.clientWidth ||
        document.body.clientWidth;
      const newHeight = window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight;
      this.$store.dispatch('setWindowWidth', newWidth);
      this.$store.dispatch('setWindowHeight', newHeight);
    },
    mounted() {
      // Load assets
      this.loadAssets();
      this.launchLoadingAnimations();
      this.checkExperimentLoading();
    },
  };
</script>

<style lang="scss">
  @import "./main";

  body { background: #000; }

  .page {
    &--animated {
      opacity: 0;
      filter: blur(100px);
      transition: filter ease-out 3s, opacity ease-out 1.5s;
    }
  }

  .loader {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: #000;
    transition: all ease .2s;
    justify-content: center;
    pointer-events: none;
    opacity: 0;

    &__text {
      color: $white;
      align-self: center;
      font-size: 14px;
      letter-spacing: 9px;
      font-family: $eestiUltraLight, sans-serif;

      span {
        filter: blur(10px);
        opacity: 0.5;
      }
    }

    &--visible {
      opacity: 1;
      pointer-events: all;
    }
  }
</style>



